import Vue from 'vue'
import Router from 'vue-router'
import RadarGallery from '@/components/radarGallery/radarGallery'
import RadarPetProfile from '@/components/radarPetProfile/radarPetProfile'
import RadarSections from '@/components/radarSections/radarSections'
import RadarUserProfile from '@/components/radarUserProfile/radarUserProfile'
import RadarTeamView from '@/components/radarTeamView/radarTeamView'

import RadarAdoptionForm from '@/components/radarAdoptionForm/radarAdoptionForm'
import RadarLostPetForm from '@/components/radarLostPetForm/radarLostPetForm'
import RadarFoundPetForm from '@/components/radarFoundPetForm/radarFoundPetForm'
import RadarEditUserProfile from '@/components/radarEditUserProfile/radarEditUserProfile'
import RadarLogin from '@/components/radarLogin/radarLogin'

Vue.component('radar-gallery', RadarGallery)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/gallery',
      name: 'RadarGallery',
      component: RadarGallery
    },
    {
      path: '/petProfile',
      name: 'petProfile',
      component: RadarPetProfile
    },
    {
      path: '/foundPetForm',
      name: 'foundPetForm',
      component: RadarFoundPetForm
    },
    {
      path: '/editUserProfile',
      name: 'editUserProfile',
      component: RadarEditUserProfile
    },
    {
      path: '/lostPetForm',
      name: 'lostPetForm',
      component: RadarLostPetForm
    },
    {
      path: '/sections',
      name: 'RadarSections',
      component: RadarSections
    },
    {
      path: '/userProfile',
      name: 'UserProfile',
      component: RadarUserProfile
    },
    {
      path: '/teamView',
      name: 'RadarTeamView',
      component: RadarTeamView
    },
    {
      path: '/adoptionForm',
      name: 'AdoptionForm',
      component: RadarAdoptionForm
    },
    {
      path: '/login',
      name: 'Login',
      component: RadarLogin
    }
  ]
})



