<template src="./App.template.pug" lang="pug"></template>

<script>
  export default {
    name: 'RadarApp',
    data () {
      return {
        toolbarItems: [
          {
            label: 'Home'
          },
          {
            label: 'Acerca de'
          }],
        isSidebarOpened: false,
        sidebarSections: [
          {
            title: 'Home'
          },
          {
            title: 'About'
          }
        ],
        mainSections: [
          {
            title: 'Galería',
            icon: '',
            path: '/gallery'
          },
          {
            title: 'Adoptar mascota',
            icon: '',
            path: '/adoptionForm'
          },
          {
            title: 'Reportar mascota perdida',
            icon: '',
            path: 'foundPetForm'
          },
          {
            title: 'Perfil de usuario',
            path: 'userProfile'
          },
          {
            title: 'Editar perfil de usuario',
            icon: '',
            path: '/editUserProfile'
          },
          {
            title: 'Equipo Radar',
            icon: '',
            path: '/teamView'
          },
          {
            title: 'Login',
            icon: '',
            path: '/login'
          }
        ]
      }
    },
    methods: {
      navigateTo: function (index) {
        console.log('navegate', index)
        this.$router.push(this.mainSections[index].path)
        this.toggleLeftSidenav()
      },
      toggleLeftSidenav () {
        this.$refs.leftSidenav.toggle()
      },
      toggleRightSidenav () {
        this.$refs.rightSidenav.toggle()
      },
      closeRightSidenav () {
        this.$refs.rightSidenav.close()
      },
      open (ref) {
        console.log('Opened: ' + ref)
      },
      close (ref) {
        console.log('Closed: ' + ref)
      }
    }
  }
</script>

<style>

  .with.top-toolbar main > .content {
    padding-top: 0;
  }

  .md-tabs .md-tab {
    padding: 0;
  }

  .list__tile {
    text-decoration: none !important;
  }

  .toolbar {
    z-index: 1000;
  }

  .md-sidenav-content {
    z-index: 10000 !important;
  }

  #toolbar-image {
    background: url('https://static1.squarespace.com/static/50c8c55be4b0a1d4330d16f8/t/53b07af3e4b03bad691bc56a/1404074741117/Cat-And-Dog-Wallpaper_6.jpg?format=1500w') center;
    height: 300px;
  }

</style>



